/**
 * 获取随机背景视频URL
 */
export function getRandomBackgroundVideo() {
    const allVideos = [];

    // 收集所有元素的视频
    Object.values(elementResources).forEach(element => {
        allVideos.push(...element.videos);
    });

    // 随机选择一个视频
    const randomIndex = Math.floor(Math.random() * allVideos.length);
    return allVideos[randomIndex];
}

/**
 * 获取随机背景音乐URL
 */
export function getRandomBackgroundMusic() {
    const allAudios = [];

    // 收集所有元素的音频
    Object.values(elementResources).forEach(element => {
        allAudios.push(...element.audios);
    });

    // 随机选择一个音频
    const randomIndex = Math.floor(Math.random() * allAudios.length);
    return allAudios[randomIndex];
}

/**
 * 获取开场视频URL
 */
export function getOpeningVideo() {
    return "https://your-cloud-storage.com/videos/opening.mp4";
}

/**
 * 获取指定星座的视频URL
 * @param {string} zodiacName - 星座英文名称，如 "aries"
 */
export function getZodiacVideo(zodiacName) {
    return VIDEO_RESOURCES.zodiacVideos[zodiacName.toLowerCase()];
}

// 根据星座获取相关资源
export function getResourcesByZodiac(zodiac) {
    // 如果提供了星座，使用星座对应的元素资源
    if (zodiac && zodiac.element) {
        return elementResources[zodiac.element.toLowerCase()];
    }

    // 否则随机选择一个元素的资源
    const elements = Object.keys(elementResources);
    const randomElement = elements[Math.floor(Math.random() * elements.length)];
    return elementResources[randomElement];
}

// 导出所有资源配置
export default {
    CLOUD_BASE_URL,
    VIDEO_RESOURCES,
    AUDIO_RESOURCES,
    getRandomBackgroundVideo,
    getRandomBackgroundMusic,
    getOpeningVideo,
    getZodiacVideo,
    getResourcesByZodiac
};